.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
  border:lightskyblue solid 5px;
}

.card:hover {
  cursor: pointer;
  box-shadow: 15px 15px 15px rgba(0, 0, 0, 0.5);
  padding: 10px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.card > .img-container {
  height: 90%;
  overflow: hidden;
  text-align: center;
  background: white;
}
.card > .img-container > img {
  width: 60%;
  margin-top: 2rem;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > p {
  padding: 10x;
  font-size: 24px;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.header {
  width: 100%;
  text-align: center;
  color: orange;
  margin-bottom: 50px;
  background-color: white;
  padding: 25px;
  padding-top: 125px;
}

.header > .title {
  font-size: 5rem;
}

.navbar {
    width: 100%;
    color: white;
    background-color: purple;
    padding: 10px;
    overflow: hidden;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
}

ul {
    list-style-type: none;
    text-align: center;    
}

li a {
    text-decoration: none;
    color: white;
}

.click, .score, .clicky {
    width: 33%;
    float: left;
    font-size: 25px;
    text-align: center;
    padding: 10px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

